function Post() {
  const match = useRouteMatch("/p/*") as any;
  const [rawMd, setRawMd] = React.useState("");
  const [isNotFound, setIsNotFound] = React.useState(false);
  const [isLoading, setIsLoading] = React.useState(true);

  useEffect(() => {
    // const url = `https://raw.githubusercontent.com/alxtz/obs-vault/main/${
    //   match.params[0]
    // }?timestamp=${Date.now()}`;
    const url = `https://raw.githubusercontent.com/alxtz/atomic-notes/main/${
      match.params[0]
    }?timestamp=${Date.now()}`;

    console.log("match", match);

    console.log("url", url);

    const fetchData = async () => {
      try {
        const response = await axios.get(url);
        let data: string = response.data;

        data = data.replaceAll("![[", "![](__imgs/");
        data = data.replaceAll(".svg]]", ".svg)");

        setRawMd(data);
        setIsNotFound(false);
        setIsLoading(false);

        const anchorId = window.location.hash;
        if (anchorId) {
          const element = document.querySelector(decodeURIComponent(anchorId));
          if (element) element.scrollIntoView();
        }
      } catch (error) {
        console.log("e", error);
        setIsNotFound(true);
        setIsLoading(false);
      }
    };

    fetchData();
  }, [match.params]);

  // return <p>{JSON.stringify(match?.params)}</p>;
  if (isLoading) {
    // return <p>Loading...</p>;
    return (
      <div className="loader">
        <div className="line"></div>
        <div className="line"></div>
        <div className="line"></div>
        <div className="line"></div>
      </div>
    );
  }

  if (isNotFound) {
    return <p>Post not found</p>;
  }

  const homeLinkElement = (
    <Link to="/">
      <HomeLinkText>~/</HomeLinkText>
    </Link>
  );

  return (
    <div className="markdown-body">
      <PageContainer>
        <BlogContainer>
          {TITLE_MAP[match.params[0]] ? (
            <>
              <BlogPostTitleContainer>
                {homeLinkElement}
                <BlogPostTitle>{TITLE_MAP[match.params[0]]}</BlogPostTitle>
              </BlogPostTitleContainer>
            </>
          ) : (
            <>
              <BlogPostTitleContainer>
                {homeLinkElement}
                <BlogPostTitle>{match.params[0]}</BlogPostTitle>
              </BlogPostTitleContainer>
            </>
          )}
          <ReactMarkdown
            children={rawMd}
            // remarkPlugins={[remarkRemoveComments, remarkGfm, remarkCodeExtra]}
            rehypePlugins={[
              rehypeRaw,
              rehypeSlug,
              [rehypeAutolinkHeadings, { behavior: "wrap" }],
            ]}
            remarkPlugins={[
              remarkRemoveComments,
              remarkGfm,
              [
                remarkGithub,
                { repository: "https://github.com/alxtz/obs-vault" },
              ],
              [remarkTruncateLinks, { style: "smart" }],
            ]}
            components={CodeBlock}
          />
        </BlogContainer>
      </PageContainer>
    </div>
  );
}

const PageContainer = styled.div``;

